	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"MDAC Insanity","date":"12 Dec 2004","slug":"/2004-12-12-mdac-insanity/","tags":"Y2004"},"html":"<p>How do people get serious work done with Windows? I have no idea...</p>\n<p>My wife wanted to update the route planning software (i.e. how many km from A to B, so she can fill out her mileage log) on her Windows XP notebook. Her company sells an atlas with software included, but she couldn't get the program to run. \"Not enough licenses, try again later.\"</p>\n<p>Smart husband decides to save time and spend 20 to get her a <a title=\"G DATA Software: PowerRoute 2004\" href=\"http://www.gdata.de/trade/productview/388/4/\">real routing program</a>. Mistake. It's the same program with a different name. \"Not enough licenses, try again later.\" The problem is mentioned in the Install FAQ: Make sure you are running as Administrator. Of course I was already running as Administrator, so that's no help.</p>\n<p>If this had been my notebook, at this point I would have installed Service Pack 2 and made sure everything was up to date. However, it's not my notebook, it's only got SP1, and the last thing I want to do is break anything; access to her company WAN is essential to her work. The setup program seems unable to detect that  <abbr title=\"Microsoft Data Access Components\">MDAC</abbr> (included with Windows) is installed, so I make sure I have the latest version and patches, and for the Jet Database Engine as well. The updates install successfully, but no luck. \"Not enough licenses, try again later.\"</p>\n<p>Finally, after a couple hours of Googling, I find a <a title=\"Frequently asked questions about MDAC 2.6 Setup\" href=\"http://support.microsoft.com/?kbid=842193\">FAQ at Microsoft</a> that covers rolling back MDAC to the original version installed with Windows (\"dasetup.exe /u\"). Never mind that it doesn't cover XP or the version of MDAC that we have. I'm desperate, and I try it. </p>\n<p>It works. Both setup programs no longer complain that MDAC is missing, and both programs are now installed and run flawlessly. I'm an experienced Sys Admin, and it only took me 6 hours to track down and correct a corrupted MDAC installation. I have no idea a normal Windows user is supposed fix something like this. Maybe <a href=\"http://radio.weblogs.com/0001011/\">Scoble</a> can tell me. I know he follows linkbacks.... <a href=\"http://radio.weblogs.com/0001011/\">Scoble?</a></p>"},"next":{"fields":{"title":"Best European Film","date":"12 Dec 2004","slug":"/2004-12-12-best-european-film/"}},"previous":{"fields":{"title":"Canada and the USA PATRIOT Act","date":"13 Dec 2004","slug":"/2004-12-13-canada-and-the-usa-patriot-act/"}}},{"node":{"fields":{"title":"Best European Film","date":"12 Dec 2004","slug":"/2004-12-12-best-european-film/","tags":"Y2004"},"html":"<p><a title=\"Kino: 'Gegen die Wand' bester europäischer Film - Kultur - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/kultur/kino/0,1518,332480,00.html\">SPON: \"Gegen die Wand\" best European film.</a> Also <a title=\"Entertainment News Article | Reuters.co.uk\" href=\"http://www.reuters.co.uk/newsArticle.jhtml?type=entertainmentNews&storyID=637121\">Reuters</a>:  </p>\n<blockquote><p>\"Gegen Die Wand\" (\"Head-On\"), about a young Turkish woman in Germany who escapes her strict Muslim home through a difficult marriage with an older man, won the best film award in the competition dubbed the \"European Oscars\" on Saturday... The European Film Awards sealed the remarkable success of \"Gegen Die Wand\", which overcame a scandal over its star's porn movie past to score a German box-office success and win major prizes including the Golden Bear at the Berlin Film Festival. As well as best film, Turkish-German Fatih Akin collected the best director award for \"Gegen Die Wand\" in the People's Choice category on Saturday.\"</p></blockquote>\n<p>Akin lives and works in Hamburg-Altona, and much of the film was shot there.</p>"},"next":{"fields":{"title":"stopping bullets with your teeth","date":"10 Dec 2004","slug":"/2004-12-10-stopping-bullets-with-your-teeth/"}},"previous":{"fields":{"title":"MDAC Insanity","date":"12 Dec 2004","slug":"/2004-12-12-mdac-insanity/"}}},{"node":{"fields":{"title":"stopping bullets with your teeth","date":"10 Dec 2004","slug":"/2004-12-10-stopping-bullets-with-your-teeth/","tags":"Y2004"},"html":"<p>I thought <a title=\"code: theWebSocket; :<br />\nWhy you should always brush your teeth\" href=\"http://ahawkins.org/2004/12/09.html\">stopping bullets with your teeth</a> happened only in cartoons. Nice story, Alwin!</p>"},"next":{"fields":{"title":"Wanna buy some slaves on ebay?","date":"09 Dec 2004","slug":"/2004-12-09-wanna-buy-some-slaves-on-ebay/"}},"previous":{"fields":{"title":"Best European Film","date":"12 Dec 2004","slug":"/2004-12-12-best-european-film/"}}}],"pathPrefix":"","first":false,"last":false,"index":367,"pageCount":1101}}

